import { Component, OnInit } from '@angular/core';
import { Http,Response } from '@angular/http';
import * as Swiper from 'swiper/dist/js/swiper.min.js';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  myWelcome = [];

  constructor(private http:Http) { }

  ngOnInit() {
    this.http.get("https://randomuser.me/api/?results=10").subscribe((response: Response) => {
      this.myWelcome = response.json().results;
      console.log(this.myWelcome);
    });
  }

  


    swiper='';
     ngAfterViewInit() {
        var swiper = new Swiper('.swiper-container');
        (<any>$('.fancyboxcls')).fancybox();
    }
}
